.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: 67px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 10;
  color: $white;
  &__logo {
    width: 204px;
    height: 31px;
    display: block;
    svg {
      width: 100%;
      height: 100%; } }
  &__right {
    a {
      width: 115px;
      height: 38px;
      border: 1px solid white;
      display: inline-block;
      margin: 0 7px;
      border-radius: 5px;
      color: white;
      line-height: 36px;
      font-weight: 700;
      text-decoration: none;
      text-align: center;
      &:hover {
        background-color: #fff;
        color: $blue; } } } }
