.page {
	width: 100%;
	height: 100%;
	position: relative;
	background-color: $blue;
	&__inner {
		position: absolute;
		top: 8.1%;
		bottom: 8.1%;
		left: 5.3%;
		right: 5.3%;
		box-shadow: 0px 30px 70px 0px rgba(64, 64, 64, 0.5);
		overflow: hidden; }
	&__left,
	&__right {
		position: absolute;
		height: 100%;
		top: 0; }
	&__left {
		position: absolute;
		width: 100%;
		transition: width 0.3s linear;
		&.is-collapsed {
			transition: width 0.5s linear;
			width: 40%;
			.pages-list {
				opacity: 0;
				pointer-events: none; }
			.selected-page {
				top: 0;
				right: 0;
				width: 100%;
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: flex-end;
				padding-right: 75px;
				transition-property: background, right, top, width, height;
				transition-duration: 0.5s, 0.5s, 0.5s, 0s, 0.5s;
				transition-timing-function: ease-in;
				transition-delay: 0s, 0s, 0s, 0.5s, 0s;
				background-color: $transparent;
				&__inner {
					position: relative;
					width: 300px; }
				&__content {
					position: relative;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0; }
				&__title {
					font-size: 40px; }
				p {
					margin-top: 35px; } }
			.route-link {
				position: relative;
				margin-top: 40px; }
			.page__object {
				width: 744px;
				height: 460px;
				left: auto;
				top: 50%;
				right: 50%;
				margin-right: -372px;
				margin-top: -315px; } } }
	&__right {
		width: 0;
		right: 0;
		&.is-open {
			width: 60%; } }
	&__content {
		width: 100%; }
	&__object {
		width: 960px;
		height: 960px;
		position: absolute;
		top: 0;
		left: 0;
		canvas {
			position: relative;
			z-index: 1; }
		&::after,
		&::before {
			content: "";
			position: absolute; }
		&.object-1 {
			&::before {
				width: 51%;
				height: 117%;
				left: 0;
				top: -3%;
				background-image: linear-gradient( 0deg, rgb(6,84,243) 0%, rgba(6,84,243,0.63) 37%, rgba(3,82,242,0.13) 87%, rgba(0,79,240,0) 100%); }
			&::after {
				width: 123%;
				height: 123%;
				left: -48%;
				bottom: -58%;
				background-image: radial-gradient(closest-side at 50% 50%, rgb(2,73,218) 0%, rgba(1,62,186,0) 100%); } }
		&.object-4 {
			&::before {
				width: 183%;
				height: 183%;
				left: -68%;
				top: -57%;
				background-image: radial-gradient(closest-side at 50% 50%, rgb(2,73,218) 0%, rgba(1,62,186,0) 100%); }
			&::after {
				width: 138%;
				height: 138%;
				left: 0%;
				bottom: -49%;
				background-image: radial-gradient(closest-side at 50% 50%, rgb(48,115,250) 0%, rgba(48,115,250,0.75) 25%, rgba(24,97,245,0.16) 84%, rgba(0,79,240,0) 100%); } } } }

.pages-list,
.selected-page {
	width: 744px;
	height: 460px;
	color: $notWhite;
	position: absolute;
	right: 20%;
	top: 178px; }

.selected-page {
	position: absolute;
	background-color: $blue90;
	box-shadow: 0px 20px 40px 0px #0042c8;
	overflow: hidden;
	line-height: 1.2;
	transition-property: background, right, top, width, height;
	transition-duration:  0.3s, 0.3s, 0.3s, 0s, 0.3s;
	transition-timing-function: ease-in;
	transition-delay: 0s, 0s, 0s, 0s, 0s;
	z-index: 1;
	&__inner {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0; }
	&__title {
		font-size: 65px;
		font-weight: 700; }
	&__content {
		position: absolute;
		top: 60px;
		right: 111px;
		bottom: 40px;
		left: 55px;
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	.route-link {
		bottom: 0;
		right: 0;
		position: absolute;
		background-color: $darkBlue; } }

.pages-list {
	padding-left: 55px;
	white-space: nowrap;
	transition: 0.3s opacity linear;
	opacity: 1;
	&__before,
	&__after {
		position: absolute;
		line-height: 70px;
		font-weight: 700; }
	$maxItems: 5;
	&__before {
		bottom: 100%;
		li {
			$j: $maxItems;
			@while $j > 0 {
				&:nth-child(#{$j}) {
					opacity: 1 - 1 / $maxItems * $j; }
				$j: $j - 1; } } }
	&__after {
		top: 100%;
		li {
			$i: $maxItems;
			@while $i > 0 {
				&:nth-child(#{$i}) {
					opacity: 1 - 1 / $maxItems * $i; }
				$i: $i - 1; } } }
	li {
		font-size: 40px;
		cursor: pointer;
		opacity: 0;
		span {
			font-size: 20px;
			display: inline-block;
			margin-left: 20px; } } }
