.tt-up-enter {
	transform: translateY(600px);
	line-height: 2.5;
	&.tt-up-enter-active {
		transform: translateY(0);
		transition: transform 1s ease, line-height 1s ease-in-out;
		line-height: 1.2;
		+ .tt-down-exit-active {
			transform: translateY(-600px); } }
	&-done {
		transform: translateY(0);
		line-height: 1.2;
		+ .tt-down-exit-done {
			transform: translateY(-600px); } } }
.tt-up-exit {
	transform: translateY(0);
	line-height: 1.2;
	&.tt-up-exit-active {
		transform: translateY(-600px);
		transition: transform 1s ease, line-height 1s ease-in-out;
		line-height: 2.5;
		position: absolute !important; }
	&-done {
		transform: translateY(-600px);
		line-height: 2.5;
		position: absolute !important; } }

.tt-down-enter {
	transform: translateY(-600px);
	line-height: 2.5;
	&.tt-down-enter-active {
		transform: translateY(0);
		transition: transform 1s ease, line-height 1s ease-in-out;
		line-height: 1.2;
		+ .tt-up-exit-active {
			transform: translateY(600px); } }
	&-done {
		transform: translateY(0);
		line-height: 1.2;
		+ .tt-down-exit-done {
			transform: translateY(600px); } } }
.tt-down-exit {
	transform: translateY(0);
	line-height: 1.2;
	&.tt-down-exit-active {
		transform: translateY(600px);
		transition: transform 0.7s ease, line-height 0.7s ease-in-out;
		line-height: 2.5;
		position: absolute !important; }
	&-done {
		transform: translateY(600px);
		line-height: 2.5;
		position: absolute !important; } }

.in-right-enter {
	transform: translateX(100%);
	&.in-right-enter-active {
		transform: translateX(0);
		transition: transform 0.5s ease-in; }
	&-done {
		transform: translateX(0); } }
.in-right-exit {
	transform: translateX(0);
	&.in-right-exit-active {
		transform: translateX(100%);
		transition: transform 0.3s ease-in; }
	&-done {
		transform: translateX(100%); } }

.pages-list {
	.pi-up-enter {
		transform: translateY(105px);
		height: 0;
		opacity: 0;
		&.pi-up-enter-active {
			transition: transform 0.5s linear, opacity 0.5s ease, height 0.5s linear;
			transform: translateY(0);
			height: 70px;
			opacity: 0.8; }
		&-done {
			transform: translateY(0);
			height: 70px;
			opacity: 0.8; } }
	.pi-up-exit {
		height: 70px;
		opacity: 0.8;
		transform: translateY(0);
		&.pi-up-exit-active {
			transition: transform 0.5s linear, opacity 0.5s ease, height 0.5s linear;
			height: 0;
			transform: translateY(-105px);
			opacity: 0; }
		&-done {
			transform: translateY(-105px);
			height: 0;
			opacity: 0; } }

	.pi-down-enter {
		transform: translateY(-105px);
		height: 0;
		opacity: 0;
		&.pi-down-enter-active {
			transition: transform 0.5s linear, opacity 0.5s ease, height 0.5s linear;
			transform: translateY(0);
			height: 70px;
			opacity: 0.8; }
		&-done {
			transform: translateY(0);
			height: 70px;
			opacity: 0.8; } }
	.pi-down-exit {
		height: 70px;
		opacity: 0.8;
		transform: translateY(0);
		&.pi-down-exit-active {
			transition: transform 0.5s linear, opacity 0.5s ease, height 0.5s linear;
			height: 0;
			transform: translateY(105px);
			opacity: 0; }
		&-done {
			transform: translateY(105px);
			height: 0;
			opacity: 0; } } }

.scroll-down {
	.pi-down-exit.pi-down-exit-active,
	.pi-down-exit-done {
		transform: translateY(-105px); } }

.scroll-up {
	.pi-up-exit.pi-up-exit-active,
	.pi-up-exit-done {
		transform: translateY(105px); } }

$i: 100;
@while $i <= 1000 {
	.delay-#{$i} {
		transition-delay: #{$i}ms; }
	$i: $i + 100; }

.scale-enter {
	transform: scale(0.2);
	opacity: 0;
	&.scale-enter-active {
		transform: scale(1);
		transition: transform 0.4s ease 0.4s, opacity 0.4s linear 0.4s;
		opacity: 1; }
	&-done {
		transform: scale(1);
		opacity: 1; } }
.scale-exit {
	opacity: 1;
	&.scale-exit-active {
		transition: opacity 0.4s ease-in-out;
		opacity: 0; }
	&-done {
		opacity: 0; } }
