.page--top-page {
	display: flex;
	align-items: center;
	height: 100%; }
.top-page {
	&__list {
		color: $notWhite;
		padding: 60px;
		width: 710px;
		li {
			font-size: 40px;
			line-height: 1.5;
			font-weight: 700;
			display: block;
			opacity: 0;
			transform: translateX(100px);
			&.is-animated {
				transition-duration: 0.3s; }
			&.in-left {
				transform: translateX(0);
				opacity: 1;
				transition-property: transform, opacity;
				transition-timing-function: ease; }
			span {
				display: block;
				font-weight: 400;
				line-height: 1.75;
				font-size: 20px;
				margin-top: 25px; }
			&:not(:first-child) {
				margin-top: 75px; } } } }
